import { Component, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, LoadingController, Loading, Platform } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { ToolsProvider } from '../../providers/tools/tools';
import { DomSanitizer } from '@angular/platform-browser';
import { ReservationServiceProvider } from '../../providers/reservation-service/reservation-service';
import { CommonServiceProvider } from '../../providers/common-service/common-service';
import { ServiceForm } from '../../model/service-form-model';
import { WellnessServiceProvider } from '../../providers/wellness-service/wellness-service';
import { HotelServiceProvider } from '../../providers/hotel-service/hotel-service';
import { NotificationProvider } from '../../providers/notification/notification';
/**
 * Generated class for the WellnessBookingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-wellness-booking',
  templateUrl: 'wellness-booking.html',
})
export class WellnessBookingPage {
  wellnessBookingForm: FormGroup;
  loading: Loading;
  item: any = [];
  res: any; //to store return data while language translate
  descriptionHtml: any;
  backgroundImage: any; // to store background image url
  suffix: any;
  personArray: any = []; // to store person for select option 
  dateType: string; // date type
  start: number;
  end: number;
  timeSlots: any = [];
  newDate: any;
  turnList: any = []; //turn list
  monthNames: any; // for month names
  monthShortNames: any = []; // for month short names
  minDate: any = new Date();
  maxDate: any;
  dayShortNames: any = [];
  firstSuffix: any;
  currencySymbol: any = this.hotelProvider.objHotel.currency.signo; // for currency symbol
  platFormIsIOS: boolean = false;
  showBackButton: boolean = true;

  constructor(public navCtrl: NavController, public navParams: NavParams, public formBuilder: FormBuilder,
    public toolsPvdr: ToolsProvider, public sanitized: DomSanitizer, public commonProvider: CommonServiceProvider,
    public reservationProvider: ReservationServiceProvider, public alertCtrl: AlertController,
    public cdRef: ChangeDetectorRef, public wellnessProvider: WellnessServiceProvider,
    public hotelProvider: HotelServiceProvider, private notificationProvider: NotificationProvider, private loadingCtrl: LoadingController, private platForm: Platform) {
    this.item = this.navParams.get('item'); // gets item 

    this.wellnessBookingForm = this.formBuilder.group({
      'specialRequests': new FormControl(null, [Validators.required]),
      'date': new FormControl(null, [Validators.required]),
      'time': new FormControl('', [Validators.required]),
      'noOfPersons': new FormControl('', [Validators.required])
    });

    // translate month names
    let monthList = this.toolsPvdr.fnLanguageTranslate("common.monthNames");
    monthList.subscribe((value: string) => {
      this.monthNames = value;
    });
    //translated month short names
    for (let i = 0; i < 12; i++) {
      this.monthShortNames.push(this.toolsPvdr.getShortMonthName(i));
    }
    //translated day short names
    for (let j = 0; j < 7; j++) {
      this.dayShortNames.push(this.toolsPvdr.getShortDayName(j));
    }
    if (this.platForm.is("ios")) {
      this.platFormIsIOS = true;
    }
  }

  ionViewWillEnter(){
    this.showBackButton = true;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad WellnessBookingPage');
    this.backgroundImage = this.item.imgPath; // returns image url
    let minPersons = this.item.modoReserva.minPersonas ? this.item.modoReserva.minPersonas : 0; // to set minimum value to select option of person
    let maxPersons = this.item.modoReserva.maxPersonas ? this.item.modoReserva.maxPersonas : 10; // to set maximum value to select option of person
    this.wellnessBookingForm.get("noOfPersons").setValue(this.item.modoReserva.minPersonas);// set minimum number of person as default value
    this.maxDate = this.toolsPvdr.fnDateFormatChange(this.commonProvider.calculateMaxDate());
    this.minDate = this.toolsPvdr.fnGetToday();
    let defaultTextTranslate = this.toolsPvdr.fnLanguageTranslate(["view.reservation.label.legal", "view.reservation.label.people.suffix", 'view.reservation.label.people.suffixFirst']); // language translation

    let defaultText = '';
    defaultTextTranslate.subscribe((value: string) => {
      defaultText = value;
    });
    // bypass HTML tags 
    this.descriptionHtml = this.sanitized.bypassSecurityTrustHtml(defaultText['view.reservation.label.legal']);

    this.suffix = defaultText['view.reservation.label.people.suffix'];
    this.firstSuffix = defaultText['view.reservation.label.people.suffixFirst'];
    this.dateType = this.item.modoReserva.tipo;

    if (this.item.modoReserva.tipo == "T") {
      this.turnList = this.item.modoReserva.turnos;
    }

    //inserts all time intervals into array timeSlots when multiple time intervals are possible
    if (this.item.modoReserva.tipo == "C" && (this.item.modoReserva.franjas.length > 0)) {
      for (let i = 0; i < this.item.modoReserva.franjas.length; i++) {
        let tmpTimeArray = this.commonProvider.fnConvertToTimeSlotsArray(this.item.modoReserva.franjas[i].start, this.item.modoReserva.franjas[i].end, this.item.modoReserva.franjas[i].increment);
        tmpTimeArray = tmpTimeArray.indexOf("0:00") > -1 ? tmpTimeArray.filter(e => e != "0:00") : tmpTimeArray;    //filters out "0:00"
        this.timeSlots = this.timeSlots.concat(tmpTimeArray);
      }

      //to set start date based on time slots available
      let lastTimeInterval = this.timeSlots.length > 0 ? this.timeSlots[this.timeSlots.length - 1] : "0:00";
      this.minDate = this.toolsPvdr.fnSetStartDate(lastTimeInterval);
    }

    //set values in personArray
    let minText = minPersons + ' ' + this.suffix;
    if (minPersons == 1) {
      minText = minPersons + ' ' + this.firstSuffix;
    }
    this.personArray.push({ text: minText, value: minPersons, checked: true });
    for (var i = minPersons + 1; i <= maxPersons; i++) {
      this.personArray.push({ text: i + ' ' + this.suffix, value: i, checked: false });
    }
    if (this.item.precio == 0) {
      this.item.price = "--";
      this.currencySymbol = "";
    }
    this.fnCheckPreEnteredFormData();
  }

  // store entered data into model before leaving page
  public ionViewWillLeave() {
    if (this.commonProvider.objFormData.wellness.bookingCompleted != true) {
      this.commonProvider.objFormData.wellness.id = this.item.id;
      this.commonProvider.objFormData.wellness.date = this.wellnessBookingForm.get('date').value;
      this.commonProvider.objFormData.wellness.time = this.wellnessBookingForm.controls['time'] && this.wellnessBookingForm.get('time').value;
      this.commonProvider.objFormData.wellness.turn = this.wellnessBookingForm.controls['turn'] && this.wellnessBookingForm.get('turn').value;
      this.commonProvider.objFormData.wellness.noOfPersons = this.wellnessBookingForm.get('noOfPersons').value;
      this.commonProvider.objFormData.wellness.comments = this.wellnessBookingForm.get('specialRequests').value;
      this.commonProvider.objFormData.wellness.bookingCompleted = false;
    }
    this.showBackButton = false;
  }
  // fetch pre-entered data into fields from model
  fnCheckPreEnteredFormData() {
    if (this.commonProvider.objFormData && this.commonProvider.objFormData.wellness) {
      this.commonProvider.objFormData.wellness.bookingCompleted = false;
      if (this.commonProvider.objFormData.wellness.id == this.item.id) {
        this.wellnessBookingForm.get("date").setValue(this.commonProvider.objFormData.wellness.date);
        this.wellnessBookingForm.controls['time'] && this.wellnessBookingForm.get("time").setValue(this.commonProvider.objFormData.wellness.time);
        this.wellnessBookingForm.controls['turn'] && this.wellnessBookingForm.get("turn").setValue(this.commonProvider.objFormData.wellness.turn);
        this.wellnessBookingForm.get("noOfPersons").setValue(this.commonProvider.objFormData.wellness.noOfPersons);
        this.wellnessBookingForm.get("specialRequests").setValue(this.commonProvider.objFormData.wellness.comments);
      }
    }
  }

  //function to go to previous page
  public fnBack() {
    this.navCtrl.pop({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }

  public fnDateChange() {

    let date = this.toolsPvdr.fnDateFormatChange(this.wellnessBookingForm.controls['date'] && new Date(this.wellnessBookingForm.get('date').value));
    let isToday: boolean;
    let today = this.toolsPvdr.fnGetToday();
    if (date == today) {
      isToday = true;
    } else {
      isToday = false;
    }

    //inserts all time intervals into array timeSlots when multiple time intervals are possible
    if (this.item.modoReserva.tipo == "C" && (this.item.modoReserva.franjas.length > 0)) {
      this.timeSlots = [];
      for (let i = 0; i < this.item.modoReserva.franjas.length; i++) {
        let tmpTimeArray = this.commonProvider.fnConvertToTimeSlotsArray(this.item.modoReserva.franjas[i].start, this.item.modoReserva.franjas[i].end, this.item.modoReserva.franjas[i].increment, isToday);
        tmpTimeArray = tmpTimeArray.indexOf("0:00") > -1 ? tmpTimeArray.filter(e => e != "0:00") : tmpTimeArray;    //filters out "0:00"
        this.timeSlots = this.timeSlots.concat(tmpTimeArray);
      }
    }
  }

  fnSubmit(item) {
    if (this.wellnessBookingForm.controls['date'].hasError('required')) {
      let validateMessages = this.toolsPvdr.fnLanguageTranslate(['view.reservation.error.nullDate', "common.error", "buttons.ok"]);

      validateMessages.subscribe((value: string) => {
        let alertnew = this.alertCtrl.create({
          title: value['common.error'],
          message: value['view.reservation.error.nullDate'],
          cssClass: 'cls_alrt',
          buttons: [
            {
              text: value['buttons.ok'],
              role: 'cancel'
            }]
        });
        alertnew.present();
        setTimeout(() => { alertnew.dismiss(); }, 5000);
      });
    } else if (this.reservationProvider.objReservation.alreadyVerifiedCheckOut) {

      let cantBookMsg = this.toolsPvdr.fnLanguageTranslate(['view.reservation.error.checkoutYaRealizado', "common.error", "buttons.ok"]);

      cantBookMsg.subscribe((value: string) => {
        let alertbook = this.alertCtrl.create({
          title: value['common.error'],
          message: value['view.reservation.error.checkoutYaRealizado'],
          cssClass: 'cls_alrt',
          buttons: [
            {
              text: value['buttons.ok'],
              role: 'cancel'
            }]
        });
        alertbook.present();
        setTimeout(() => { alertbook.dismiss(); }, 5000);
      });

    } else {
      let confirmMessages = this.toolsPvdr.fnLanguageTranslate(['buttons.send', 'buttons.modify', 'view.reservation.confirm', 'common.confirm']); //for language translation

      confirmMessages.subscribe((value: string) => {
        let alertnew = this.alertCtrl.create({
          title: value['common.confirm'],
          message: value['view.reservation.confirm'],
          cssClass: 'cls_alrt',
          buttons: [
            {
              text: value['buttons.modify'],
              role: 'cancel',
              handler: data => {

              }
            },
            {
              text: value['buttons.send'],
              handler: data => {
                //date format changes for nostay user
                let bookingDate: any;
                let time = this.wellnessBookingForm.controls['time'] && this.wellnessBookingForm.get('time').value ? this.wellnessBookingForm.get('time').value + ':00' : '00:00:00';

                bookingDate = this.toolsPvdr.dateFormatChangeForService(this.wellnessBookingForm.controls['date'] && new Date(this.wellnessBookingForm.get('date').value)).split(" ")[0] + ' ' + time;

                if ((item.paymentRequiredNoStay && this.reservationProvider.objReservation.noStayGuest) || (item.paymentRequired && !this.reservationProvider.objReservation.noStayGuest)) { // if payment required is true then redirecting to payment form
                  //commonProvider
                  this.commonProvider.objServiceForm = new ServiceForm();
                  this.commonProvider.objServiceForm.serviceId = item.id;
                  this.commonProvider.objServiceForm.date = bookingDate;
                  this.commonProvider.objServiceForm.turn = this.wellnessBookingForm.controls['turn'] && this.wellnessBookingForm.get('turn').value ? this.wellnessBookingForm.get('turn').value : '';
                  this.commonProvider.objServiceForm.noOfPersons = this.wellnessBookingForm.get('noOfPersons').value;
                  this.commonProvider.objServiceForm.comments = this.wellnessBookingForm.get('specialRequests').value;
                  this.navCtrl.push("WellnessPaymentPage", { item: item }, { animate: true, animation: 'transition', duration: 500, direction: 'forward' });

                } else {
                  // if payment required is false 
                  this.showLoading();
                  console.log(this.wellnessBookingForm.get('noOfPersons').value);
                  let serviceId = item.id;
                  let date = bookingDate;
                  let itemServiceId = item.servicioSaludId;
                  let turn = this.wellnessBookingForm.controls['turn'] && this.wellnessBookingForm.get('turn').value ? this.wellnessBookingForm.get('turn').value : '',
                    person = this.wellnessBookingForm.get('noOfPersons').value,
                    comments = this.wellnessBookingForm.get('specialRequests').value,
                    reservationId = this.reservationProvider.objReservation.reservationId;


                  this.wellnessProvider.sendWellnessReservation(serviceId, date, turn, person, comments, reservationId, itemServiceId).subscribe(data => {
                    this.loading.dismiss();
                    if (data.success == true) {
                      this.wellnessBookingForm.reset();
                      this.commonProvider.objFormData.wellness = [];
                      this.commonProvider.objFormData.wellness.bookingCompleted = true;
                      // success message translate
                      let successMessages = this.toolsPvdr.fnLanguageTranslate(['view.reservation.success', "common.requestReceived", "buttons.ok"]);

                      successMessages.subscribe((value: string) => {
                        let alertsuccess = this.alertCtrl.create({
                          title: value['common.requestReceived'],
                          message: value['view.reservation.success'],
                          cssClass: 'cls_alrt',
                          buttons: [
                            {
                              text: value['buttons.ok'],
                              role: 'cancel',
                              handler: data => {
                                this.navCtrl.popTo(this.navCtrl.getByIndex(1), { animate: true, animation: 'transition', duration: 500, direction: 'back' });
                              }
                            }]
                        });
                        alertsuccess.present();
                        setTimeout(() => {
                          alertsuccess.dismiss();
                          this.navCtrl.popTo(this.navCtrl.getByIndex(1), { animate: true, animation: 'transition', duration: 500, direction: 'back' });
                        }, 5000);
                      });
                    }
                  });
                }

              }
            }]
        });
        alertnew.present();
        setTimeout(() => { alertnew.dismiss(); }, 5000);
      });

    }
  }

  //Loader
  public showLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent'
    });

    this.loading.present();
  }
}



